import { Component, OnInit } from '@angular/core';

import {MatDialog} from '@angular/material';
import { LoginComponent } from '../login/login.component';

@Component({
    selector:'app-home',
    templateUrl:'./home.component.html',
    styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit{

    
    showing =true;
    coming =false;

    Showing(){
      this.showing =true;
      this.coming =false;
    }
  
  
    Coming(){
      this.showing =false;
      this.coming =true;
    }
  
    constructor(public dialog:MatDialog) { }

    //Movie_setting
    customOptions: any = {
    loop: true,
    mouseDrag: false,
    touchDrag: false,
    pullDrag: false,
    autoHeight: true,
    autoWidth:true,
    autoplay:true,
    dots: true,
    navSpeed: 1000, 
   
    responsive: {
      0: {
        items: 1
      },
    },
    nav: false
  }

  customOptionstest: any = {
    loop: true,
    mouseDrag: false,
    touchDrag: false,
    pullDrag: false,    
    autoplay:true,
    autoplayTimeout:1000,
    autoplayHoverPause:false,
    dots: false,
    navSpeed: 1000,
    
    responsive: {
        0: {
          items: 1
        },
        1: {
          items: 2
        },
        2: {
          items: 3
        },
        3: {
          items: 4
        },
        4: {
          items: 5
        }
      },
    nav: false
  }
  //Movie_setting

    

    ngOnInit(){

    }

    openLogin(){
        //this.dialog.open(LoginComponent)
        let dialogRef = this.dialog.open(LoginComponent, {
            height: 'fit-content',
            width: '390px',
          });
    }

    
}