import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';
import { map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(
    private httpClient: HttpClient
  ) {
  }

  saveUser(user:any): Observable<any> {
    
    return this.httpClient.post("http://localhost:8080/registration",user);
  }

  loginUser(user:any): Observable<any> {
    const headers = new HttpHeaders({ Authorization: 'Basic ' + btoa(user.email + ':' + user.password) });
    return this.httpClient.post("http://localhost:8080/login",user,{headers:headers}).pipe(
      map(
        (        userData: any) => {
          sessionStorage.setItem('email', user.email);
          let authString = 'Basic ' + btoa(user.email + ':' + user.password);
          sessionStorage.setItem('basicauth', authString);
          return userData;
        }
      )

    );
  }
}
