import { Component, OnInit } from '@angular/core';
import { UserService } from '../user.service';
import {Router} from '@angular/router';
import {MatDialog} from '@angular/material';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  

  constructor(private userService:UserService,private router:Router,public dialogRef:MatDialog,public snackBar: MatSnackBar){}

  ngOnInit() {
  }

  public user:any={};

  loginUser(user:any){
    this.userService.loginUser(user).subscribe((response) =>{
    if(response){
        if(response.token){
            // localStorage.setItem('currentUser',JSON.stringify(response.token));
            
            if(response.user.role === 'ADMIN'){
                this.router.navigate(['/admin']);
                this.dialogRef.closeAll();
                this.snackBar.open("Admin Login Successfully", "close", {
                    duration: 2000,
                  });
            }else{
                this.router.navigate(['/user']);
                this.dialogRef.closeAll();
                this.snackBar.open("User Login Successfully", "close", {
                  duration: 2000,
                });
            }
        }
    }else{
        this.snackBar.open(response, "close", {
            duration: 2000,
          });
    }
    }, error => {
        
        this.snackBar.open("Username & Password is incorrect", "close", {
            duration: 2000,
          });
    })
}

}
